import React from "react";
import Hero from "./IndexSections/Hero";

class RaffleTicketModel extends React.Component {
    // Handles loading the events for <model-viewer>'s slotted progress bar
    onProgress = (event) => {
        const progressBar = event.target.querySelector('.progress-bar');
        const updatingBar = event.target.querySelector('.update-bar');
        updatingBar.style.width = `${event.detail.totalProgress * 100}%`;
        if (event.detail.totalProgress === 1) {
            progressBar.classList.add('hide');
        } else {
            progressBar.classList.remove('hide');
            if (event.detail.totalProgress === 0) {
                if(event.target.querySelector('.center-pre-prompt'))
                event.target.querySelector('.center-pre-prompt').classList.add('hide');
            }
        }
    };

    componentDidMount() {
        //this.$el = $(this.el);
        //this.$el.somePlugin();
        document.querySelector('model-viewer').addEventListener('progress', this.onProgress);
    }

    componentWillUnmount() {
        document.querySelector('model-viewer').removeEventListener('progress');
    }

    render() {
        return (
            <div>
                <model-viewer style={{width: 350, height: 450, backgroundColor: 'transparent' }} bounds="tight" src="/webview/PunkRaffle_l.glb" ar ar-modes="webxr scene-viewer quick-look" camera-controls auto-rotate environment-image="neutral" poster="/webview/poster.webp" shadow-intensity="1">
                    <div className="progress-bar hide" slot="progress-bar">
                        <div className="update-bar"></div>
                    </div>
                    <button slot="ar-button" id="ar-button" style={{ backgroundColor: 'transparent'}}>
                        View in your space
                    </button>
                    <div id="ar-prompt">
                        <img src="/webview/ar_hand_prompt.png" />
                    </div>
                </model-viewer>
            </div>
        )
    }
}

export default RaffleTicketModel;